// btn top +
.bl_ancor {
  position: fixed;
  bottom: 1em;
  right: 1.2em;
  width: 2.5em;
  height: 2.5em;
  z-index: 999;
}

.bl_ancor__link {
  width: 100%;
  height: 100%;
  display: block;

  &:hover {
    .icon-arrow__up {
      background-color: $blue;
    }
  }
}

.icon-arrow__up {
  position: absolute;
  bottom: 0;
  left: 0;
  color: #eee;
  width: 100%;
  height: 100%;
  background-color: $brand-color;
  border-radius: 50%;
  box-shadow: 0 0 1px 9px rgba(10, 75, 139, 0.17);
  z-index: -1;
  transition: .45s;

  &::after {
    position: absolute;
    top: 0;
    bottom: .9em;
    right: 0;
    left: 0;
    margin: auto;
    content: "";
    width: 0;
    height: 0;
    border-bottom: .9em solid transparent;
    transition: .25s;
    border: .6em solid transparent;
    border-bottom: 1.1em solid white;
  }
}
